<template>
  <div style="min-height: 600px">
    <el-card shadow="never" style="margin-bottom: 20px">
      <el-form ref="form" :model="form" label-width="80px" :rules="ruleValidate">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="form.description" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <mavon-editor
            @imgAdd="imgAdd"
            style="max-height: 500px"
            ref="md"
            v-model="form.content"
            :subfield="false"
            :toolbars="mavonEditorToolbars"
          /> -->
          <tinymce v-model="form.content" :height="300" />
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="onSubmit"
            :loading="submitButton.loading"
            :disabled="submitButton.disabled"
          ></el-button>
          <el-button @click="$router.push('/user/blog/main')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <token-dialog ref="tokenDialog"></token-dialog>
  </div>
</template>
<script>
import Tinymce from "@/components/Tinymce";
import { mapGetters } from "vuex";
import TokenDialog from "@/views/common/TokenDialog";
import GistApi from "@/api/gist";
export default {
  components: {
    TokenDialog,
    Tinymce
  },
  data() {
    return {
      form: {
        title: "",
        description: "",
        content: ""
      },
      ruleValidate: {
        title: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "string",
            max: 32,
            message: "32",
            trigger: "change"
          }
        ],
        description: [
          { required: true, message: "", trigger: "blur" }
        ],
        content: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      submitButton: {
        loading: false,
        disabled: false
      },
      mavonEditorToolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        // fullscreen: true, // 
        // readmodel: true, // 
        htmlcode: true, // html
        // help: true, //  /* 1.3.5 */
        // undo: true, // 
        // redo: true, // 
        trash: true, // 
        // save: true, // eventssave /* 1.4.2 */
        // navigation: true, //  /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, //  /* 2.2.1 */
        // subfield: true, // 
        preview: true // 
      }
    };
  },
  computed: {
    ...mapGetters(["token"])
  },
  mounted() {
    if (!this.token) {
      this.$nextTick(() => {
        this.$message({
          message: "",
          type: "error"
        });
        this.$router.go(-1);
      });
      return;
    }
  },
  methods: {
    imgAdd(pos, file) {
      this.$refs.md.$img2Url(pos, file.miniurl);
    },
    onSubmit() {
      if (this.token) {
        this.publish();
      } else {
        this.$refs.tokenDialog.open(() => {
          this.publish();
        });
      }
    },
    publish() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          this.submitButton.loading = true;
          this.submitButton.disabled = true;
          GistApi.create(this.form)
            .then(response => {
              let result = response.data;
              // console.log(JSON.stringify(result))
              this.$message({
                message: "",
                type: "success"
              });
              this.$router.push("/user/blog/details/" + result.id);
            })
            .then(() => {
              this.submitButton.loading = false;
              this.submitButton.disabled = false;
            });
        }
      });
    }
  }
};
</script>

<style>
</style>



